.container{
    margin: 50px 0;

}
.card{
    margin: 0 2% !important;
}

.infected{
    border-bottom: 8px solid rgba(0, 0, 255, 0.7);
}

.recovered{
    border-bottom: 8px solid rgba(0, 255, 0, 0.7);
}
.deaths{
    border-bottom: 8px solid rgba(255, 0, 0, 0.7);
    
}
@media(max-width: 770px){
    .card{
        margin: 2% 0 !important;
        padding: 0 10px !important;
    }
}